<!--
组件向外提供接口事件有：
1、结算按钮触发事件
2、数量+按钮触发事件
3、数量-按钮触发事件
4、删除按钮触发事件
5、清空按钮触发事件
6、记录按钮触发事件
7、会员按钮触发事件
-->

<template>
    <div>

            <div class="itemContainer" style="margin-top:10px;">
                <i-button @click="$emit('order-settle')" class="toolBtn" size="large" long type="primary" vertical="true">结算</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('item-add')" class="toolBtn" size="large" long type="warning" vertical="true">+</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('item-sub')" class="toolBtn" size="large" long type="error" vertical="true">-</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('item-del')" class="toolBtn" size="large" long vertical="true">删除</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="$emit('order-del')" class="toolBtn" size="large" long vertical="true">清空</i-button>
            </div>

            <div class="itemContainer">
                <i-button @click="showDrawer()" class="toolBtn" size="large" long vertical="true">记录</i-button>
            </div>

            <div class="itemContainer">
                <i-button class="toolBtn" size="large" long vertical="true">会员</i-button>
            </div>

            <div class="itemContainer" style="margin-top:200px;">
                <i-button class="toolBtn" size="large" long vertical="true">更多</i-button>
            </div> 

                <Drawer title="订单【列表】" placement="left" :closable="false" v-model="value2" width="360">

                    <div v-for="(item, index) in orderInfoList" :key="index">

                        <Card style="margin-bottom:10px;" :padding=10 >
                            <div @click='showOrderDetail(index)' >
                                <Row>
                                    <i-col span="14">
                                        订单编号: {{item.orderId}}
                                    </i-col>
                                    <i-col span="10" style="text-align: right;" >
                                        顾客: {{item.menberId}}
                                    </i-col>
                                </Row>
                                <Row>
                                    <i-col span="14">
                                        超市编号: {{item.shopId}}
                                    </i-col>
                                    <i-col span="10" style="text-align: right;" >
                                        总金额: ￥{{item.orderAmount}}
                                    </i-col>
                                </Row>
                                <Row>
                                    <i-col span="24">
                                        下单时间: {{item.orderTime}}
                                    </i-col>
                                </Row>
                            </div>
                        </Card>
                    </div>

                </Drawer>
                
                <Drawer title="订单【详情】" placement="left" :closable="false" v-model="value6">

                    <div>基本信息</div>
                    <div>
                        <div style="font-size:15px;">单号: {{ClickedOrderId}}</div>
                        <div style="font-size:15px;">总金额: ￥{{ClickedOrderAmount}}</div>
                    </div>
                    <Divider>商品列表</Divider>
                    <div v-for="(item, index) in orderDetailList" :key="index">

                        <Card style="margin:5px 0px;" >
                            <div>
                                <Row>
                                    <i-col span="6">
                                        <img src="../assets/commodity.png" >
                                    </i-col>
                                    <i-col span="12" >
                                        {{item.itemTitle}}
                                    </i-col>
                                    <i-col span="6" >
                                        <div style="width:100%; text-align: right;">￥{{item.itemPrice}}</div>
                                        <div style="width:100%; text-align: right;">×{{item.itemNum}}</div>
                                        <div style="width:100%; text-align: right; color:red;" >￥{{item.itemPrice*item.itemNum}}</div>
                                    </i-col>
                                </Row>
                            </div>

                        </Card>
                        
                    </div>

                </Drawer>

    </div>

    
</template>

<script>
export default {

    name:"orderToolbar",
    data(){
        return {
            pageSize: 10,
            value2: false,
            value6: false,
            orderInfoList: [],
            orderDetailList: [],
            ClickedOrderIndex: 0,
            ClickedOrderId: '',
            ClickedOrderAmount: 0,
        }
    },
    methods:{
        showDrawer(){
            var pageSize = this.pageSize ;
            this.$http.get('http://localhost:8080/order/getOrderList',
                {
                params: {

                    'page': 0 ,

                    'size': pageSize

                }
                }).then(res=>{
                    // 请求成功回调
                    console.log(res)
                    this.orderInfoList = res.body.content;
                    this.value2 = true ;

                },resErr=>{

            });

        },
        showOrderDetail(index){
            var _this = this ;
            this.ClickedOrderIndex = index ;
            this.ClickedOrderId = this.orderInfoList[index].orderId ;
            this.ClickedOrderAmount = this.orderInfoList[index].orderAmount;
            //console.log(orderId)

            this.$http.get('http://localhost:8080/order/getOrderDetail',
                {
                    params: {
                        orderId: _this.orderInfoList[index].orderId
                    }
                }).then(res=>{
                    // 请求成功回调
                    console.log(res)
                    this.orderDetailList = res.body;
                    this.value6 = true ;

                },resErr=>{

            });

            //this.value6 = true ;
        }
    }

}
</script>

<style scoped>

img{
	width: auto;
	height: auto;
	max-width: 100%;
	max-height: 100%;	
}


.itemContainer{
    width: 100%;
    padding: 5px 10px;

}

.toolBtn{
    display: block;
    font-size: 18px;
}



.orderItem{
    width:100%;
    padding:0px 5px;
    font-size: 13px;
    border:solid 0px #ddd;
    border-bottom-width: 1px;
}

.centerItem{
    margin-top:20px;
    display: flex;
    justify-content: center;
    align-items: center;
}


</style>
