<template>

    <Layout id="app">
        <Spin size="large" fix v-if="spinShow"></Spin>
        <!-- <Modal v-model="modal"></Modal> -->
        <Modal v-model="modal" width="500" >
            <p slot="header" style="text-align:center">
                <span>搜索结果</span>
            </p>

            <div id="goodsList">
                
                <div v-for="(item, index) in searchList" :key="index" @click="addGoods(item)"  >
                    <Card  class="goodsItem" >
                        <div class="itemTop">
                        <div><img src="./assets/commodity.png" style="width:40px;"></div>
                        <div style="text-align:right;">
                            <div style="color:red;">￥{{item.price}}</div>
                            <div>库存:{{item.amount}}</div>
                        </div>
                        </div>
                        <div class="itemBotton">
                        <div>{{item.name}}</div>
                        </div>
                    </Card>
                </div>

            </div>

            <div slot="footer">
                <Page :current="searchResultPage" :total="searchResultTotal" simple v-on:on-change="changePage" />
            </div>
        </Modal>

      <Header id="header">

                <!-- <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo"></div>
                    <div class="layout-nav">
                        <MenuItem name="1">
                            <Icon type="ios-navigate"></Icon>
                            开会员卡
                        </MenuItem>
                        <MenuItem name="2">
                            <Icon type="ios-keypad"></Icon>
                            
                        </MenuItem>
                        <MenuItem name="3">
                            <Icon type="ios-analytics"></Icon>
                            Item 3
                        </MenuItem>
                        <MenuItem name="4">
                            <Icon type="ios-paper"></Icon>
                            Item 4
                        </MenuItem>
                    </div>
                </Menu> -->


      </Header>

      <Layout>

          <Sider id="sider" width="250">

            <Card style="background-color:inherit; border-color:inherit;">
                <i-input placeholder="搜索商品" search v-on:on-enter="searchProduct()" v-model="searchContent" />
            </Card>

                <div style="font-size:15px; padding:3px 10px; background-color:white;">
                    <div class="itemTop">
                        <div>日期: {{currTime}}</div>
                    </div>
                    <div  class="itemBotton">
                        <div>操作员: {{salesmanId}}</div>
                        <div>超市: {{superMarketID}}</div>
                    </div>
                </div>

                <Card style="height:80px; border-radius:0px;">
                    <div class="itemTop">
                        <div>订单号:</div>
                        <div>{{orderId}}</div>
                    </div>
                    <div class="itemBotton">
                        <div>合计:</div>
                        <div style="color:red; font-size:20px;">￥{{amount}}</div>
                    </div>  
                </Card>

            <Scroll style="background-color:white;" height=440>
            
                <div v-for="(item, index) in list" :key="index">
                    <div class="orderItem" @click='clickOrderItem(index)' :style=" (index == onClickOrderItem) ? 'background-color:#97ACD4;' : '' ">
                        <Row>
                            <i-col span="2">
                                <div class="centerItem">{{index+1}}</div>
                            </i-col>
                            <i-col span="18" offset="1">
                                <Row>
                                    <i-col span="18">{{item.name}}</i-col>
                                </Row>
                                <Row>
                                    <i-col span="20">编号: {{item.id}}</i-col>
                                </Row>
                                <Row>
                                    <i-col span="14">单价: {{item.price}}￥</i-col>
                                    <i-col span="10" style=" font-weight: 700;">小结: {{item.summary}}￥</i-col>
                                </Row>
                            </i-col>
                            <i-col span="3">
                                <div class="centerItem" style="font-size:15px;">×{{item.num}}</div>
                            </i-col>
                        </Row>

                    </div>
                    <!-- <Divider></Divider> -->
                </div>

            </Scroll>

          </Sider>

          <Sider width="100">

            <orderToolbar 
            v-on:order-settle="orderSettlement()"
            v-on:item-add="orderItemNumAdd()"
            v-on:item-sub="orderItemNumSub()"
            v-on:item-del="orderItemDel()"
            v-on:order-del="orderDel()" />

          </Sider>

          <Layout>

            <Content id="content">

              <product v-on:add-goods="addGoods($event)"/>

            </Content>

            <Footer id="footer">
                ©Howie-Shen 2018
            </Footer>

          </Layout>

      </Layout>

    </Layout>

</template>

<script>

import product from './components/product.vue'
import orderToolbar from './components/orderToolbar.vue'

export default {
  name: 'app',
  components: {
    product,
    orderToolbar
  },

// {
//       id: '0000001',
//       name: '脉动',
//       price: 15,
//       num: 1,
//       summary: 15
//}
    data() {
          return {

            //   搜索后弹出模态框属性
              searchList: [],

              searchContent: "",

              searchResultPage: 1 ,

              searchResultSize: 6 ,

              searchResultTotal: 0,

              modal: false,

              spinShow: false,

            // 订单区域属性
              superMarketID : "001" ,

              salesmanId : "007" ,

              onClickOrderItem: null,

              LastClickOrderItem: null,

              currTime: null,

              amount: null,
              
              list: []
      }
    },

    //初始化
    created(){
      var _this = this ;
      //设置时钟
			setInterval(function(){
				_this.currTime = _this.formatTime( new Date() ) ;
      },990)

      //初始化金额
      var amount = 0;
      for(var i=0 ; i < this.list.length ; i++){
        amount += this.list[i].summary ;
      }
      this.amount = amount ;
      this.orderId = Date.parse( new Date() )/1000 + this.superMarketID ;
    },

    methods:{
        
        //通用函数

        formatNumber(n) {
          n = n.toString()
          return n[1] ? n : '0' + n
        },

        formatTime(date) {
          var year = date.getFullYear() 
          var month = date.getMonth() + 1 
          var day = date.getDate() 
          var hour = date.getHours() 
          var minute = date.getMinutes()  
          var second = date.getSeconds() 
          return [year, month, day].map(this.formatNumber).join('/') + ' ' + [hour, minute, second].map(this.formatNumber).join(':')
        },


        // 视图控制

        clickOrderItem(data){
            this.LastClickOrderItem = this.onClickOrderItem
            this.onClickOrderItem = data

            if(this.LastClickOrderItem == data)
                return this.onClickOrderItem = null ;

            //console.log(data)
        },

        //---------------------逻辑控制---------------------


        //商品项数量+1
        orderItemNumAdd: function(){
            if(this.onClickOrderItem == null) {
                this.$Notice.warning({title: '请先选中订单中的商品项',duration: 1.5});   
                return ;
            }
            this.list[this.onClickOrderItem].num++ ;
            this.list[this.onClickOrderItem].summary += this.list[this.onClickOrderItem].price ;
            this.amount += this.list[this.onClickOrderItem].price ;
        },
        //商品项数量-1
        orderItemNumSub(){
          if(this.onClickOrderItem == null){
            this.$Notice.warning({title: '请先选中订单中的商品项',duration: 1.5});
            return ;
          }
          
            if(this.list[this.onClickOrderItem].num == 1){
              return ;
            }
            this.list[this.onClickOrderItem].num-- ;
            this.list[this.onClickOrderItem].summary -= this.list[this.onClickOrderItem].price ;
            this.amount -= this.list[this.onClickOrderItem].price ;
        },
        //删除订单商品项
        orderItemDel(){
          if(this.onClickOrderItem == null) return ;
          this.amount -= this.list[this.onClickOrderItem].summary ;
          this.list.splice(this.onClickOrderItem,1) 
        },
        //清空订单
        orderDel(){
          this.amount = 0 ;
          this.list = [] ;
        },

        //添加商品到订单
        addGoods(data){
            console.log(data);
            var sign = 0 ;
            for(var i=0;i<this.list.length;i++){
                if( this.list[i].id == data.id )
                {
                    sign = 1 ;
                    //UI提示已存在
                    this.$Notice.warning({
                        title: '订单中已存在该商品',
                        duration: 1.5
                    });
                    break ;
                }
            }
            if(!sign){
                data.num = 1 ;
                data.summary = data.price ;
                this.amount += data.price ;
                this.list.push(data) ;
            }
        },

        //提交订单
        orderSettlement(){
            this.spinShow = true ;
            var _this = this ;

            //订单商品信息
            var orderItemList = [] ;
            var goodsTotalCost = 0;
            
            for(var i=0;i<this.list.length;i++) {
                var orderItem = {} ;
                orderItem.orderId = this.orderId ;
                orderItem.itemId = this.list[i].id ;
                orderItem.itemTitle = this.list[i].name ;
                orderItem.itemNum = this.list[i].num ;
                orderItem.itemPrice = this.list[i].price ;
                orderItem.itemDiscount = 1 ;
                orderItem.itemImg = this.list[i].picUrl ;
                orderItem.itemAmout = this.list[i].summary ; 
                goodsTotalCost += this.list[i].cost ;
                orderItemList.push(orderItem) ;
                
            }

            //订单基本信息
            var orderInfo = {} ;

            orderInfo.orderId = this.orderId ;
            orderInfo.salesmanId = this.salesmanId ;
            orderInfo.shopId = this.superMarketID ;
            orderInfo.menberId = null ;
            orderInfo.menberName = null ;
            orderInfo.menberPhone = null ;
            orderInfo.menberDiscount = 1 ;
            orderInfo.goodsTotalCost = goodsTotalCost ;  //商品总成本
            orderInfo.goodsTotalPrice = this.amount ;
            orderInfo.orderDiscount = 1 ;
            orderInfo.orderAmount = this.amount ;
            orderInfo.type = 1 ;

            //console.log(orderInfo)
            console.log(orderItemList)

            this.$http.post('http://localhost:8080/order/save',
            {
                "orderInfo" : orderInfo ,
                "orderDetailList": orderItemList
            }).then(res=>{
                _this.spinShow = false
                // 请求成功回调
                console.log(res)
                //展示下单结果
                this.$Modal.success({title: "购买成功",content: "content"});
                //生成新的订单号
                _this.orderId = Date.parse( new Date() )/1000 + this.superMarketID ;
                //清空订单
                _this.orderDel() ;
                
            },resErr=>{

            });


        },


        //搜索商品
        searchProduct(){
            //console.log( this.searchContent ) ;
            this.modal = true ;
            var name = this.searchContent ;
            var page = this.searchResultPage - 1 ;
            var size = this.searchResultSize ;
            this.$http.get('http://localhost:8080/commodity/search',
                {
                params: {
                    'name' : name,

                    'page': page ,

                    'size': size
                }
                }).then(res=>{
                // 请求成功回调
                console.log(res)
                this.searchList  = res.body.commodityList.content ;
                this.searchResultTotal = res.body.commodityList.totalElements ;
                },resErr=>{

            });

        },

        //改变搜索结果分页
        changePage(data){

            console.log(data);

            var name = this.searchContent ;

            var pageSize = this.searchResultSize ;

            this.$http.get('http://localhost:8080/commodity/search',
                {
                params: {
                    'name' : name ,

                    'page': data-1 ,

                    'size': pageSize
                }
                }).then(res=>{
                // 请求成功回调

                console.log(res)

                this.searchList  = res.body.commodityList.content ;

                },resErr=>{

            });
        }

    }
    
}

</script>

