<template>
  <div style="position: relative">

      <div id="goodsType">

        <scrollView v-on:change-type="changeType($event)"/>

      </div>

      <div id="goodsList">

        <div v-for="(item, index) in list" :key="index" >
          <div  @click="$emit('add-goods',item)" >
          <Card  class="goodsItem" >
            <div class="itemTop" >
              <div style="width:20%;"><img src="../assets/commodity.png"></div>
              <div style="text-align:right;">
                <div style="color:red;">￥{{item.price}}</div>
                <div>{{item.amount}}个</div>
              </div>
            </div>
            <div class="itemBotton">
              <div>{{item.name}}</div>
              <div>({{item.id}})</div>
            </div>
          </Card>
          </div>
        </div>

      </div>

      <div id="pageBar">
        <Page :total="totalItem" :page-size=pageSize  show-elevator v-on:on-change="changePage" />
