      </div>
  <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
import scrollView from './scrollView.vue'
import globalValue from '../utils/Global.vue'

export default {
    components:{
      scrollView
    },
    data(){
      return {
        list:[] ,
        pageSize: 20,
        typeIndex:0 ,
        totalItem: 0,
        spinShow: false
      }
    },
    created: function(){
      
    },
    methods:{

      changeType(data){
        this.spinShow = true ;

        this.typeIndex = data ;

        var changeType = this.typeIndex ;

        var pageSize = this.pageSize

          this.$http.get('http://localhost:8080/commodity/listByType',
            {
              params: {
                'commodityType' : changeType,

                'page': 0 ,

                'size': pageSize
              }
            }).then(res=>{
              // 请求成功回调
              console.log(res)
              
              this.list = res.body.commodityList.content ;
              this.totalItem = res.body.commodityList.totalElements ;
              this.spinShow = false ;
            },resErr=>{

          });

        
        // this.$http({
        //     url: 'http://localhost:8080/commodity/listByType', //?commodityType=2&page=0&size=2
        //     method: 'GET',
