        //     // 请求成功回调
        // }, function () {
        //     // 请求失败回调
        // });

      },
      changePage(data){

        console.log(data)
        var changeType = this.typeIndex ;

        var pageSize = this.pageSize ;

          this.$http.get('http://localhost:8080/commodity/listByType',
            {
              params: {
                'commodityType' : changeType,

                'page': data-1 ,

                'size': pageSize
              }
            }).then(res=>{
              // 请求成功回调

              console.log(res)
              this.list = res.body.commodityList.content ;

            },resErr=>{

          });

        // this.$http({
        //     url: 'http://localhost:8080/commodity/listByType', //?commodityType=2&page=0&size=2
        //     method: 'GET',
        //     // header:{
        //     //     ContentType: "application/json charset=UTF-8"
